<template>
  <div id="Protocol">
      <header><van-icon name="arrow-left" @click="colseHide"/></header>
      <main>
          <div class="title"></div>
          <div class="item">
              <h4></h4>
              <p>1</p>
              <p>2</p>
              <p>3</p>
          </div>
          <div class="item">
              <h4></h4>
              <p>1</p>
              <p class="tips"></p>
              <p>2</p>
              <p class="tips"></p>
              <p>3</p>
              <p class="tips"></p>
              <p class="tips warn"></p>
          </div>
          <div class="item">
              <h4></h4>
              <p></p>
          </div>
          <div class="item">
              <h4></h4>
              <p>1</p>
              <p class="tips">1)</p>
              <p class="tips">2)</p>
              <p class="tips">3)</p>
              <p>2</p>
              <p></p>
          </div>
      </main>
  </div>
</template>

<script>
import {Icon} from 'vant'
export default {
  name: 'Protocol',
  components: {
    VanIcon: Icon
  },
  methods: {
    colseHide () {
      this.$emit('listenProtocol', false)
    }
  }
}
</script>

<style lang="scss">
 @import '../../style/base.scss';
  #Protocol{
    width:100%;
    height:100%;
    header{
      width:100%;
      height:px2rem(150px);
      background: #fe6699;
      @include font-dpr(20px);
      color:white;
      line-height: px2rem(150px);
      text-align: center;
      .van-icon{
          left:3%;
          right:0;
          text-align: left;
          color:white;
          @include font-dpr(22px);
          top:px2rem(40px);
      }
    }
    main{
        padding:px2rem(40px);
        .title{
            @include font-dpr(16px);
        }
        h4{
           @include font-dpr(14px);
           color:#333;
           padding:px2rem(20px) 0;
        }
        p{
            padding-left:15px;
            color:#444;
            line-height: 20px;
            @include font-dpr(12px);
        }
        .warn{
            color:#fab803;
        }
        .tips{
            padding-left:30px;
        }
    }
  }
</style>



